import React, { useState, useRef, useCallback, useEffect } from 'react';
import TodoTemplate from './TodoTemplate';
import TodoInset from './TodoInset';
import TodoList from './TodoList';
const App = () => {

  const [todos, setTodos] = useState([
    // {id:1, text:'일정관리1', checked:true},
    // {id:2, text:'일정관리2', checked:false},
    // {id:3, text:'일정관리3', checked:true}
  ])
  const nextid = useRef(1)
  
  const onToggle = useCallback((id) => {
    setTodos(todos.map( todo => todo.id === id ? { ...todo, checked:!todo.checked}: todo))

    localStorage.kim = JSON.stringify(todos.map( todo => todo.id === id ? { ...todo, checked:!todo.checked}: todo))
  }
    , [todos])
  
  
  const onInsert = useCallback( (value) => {
    const todo = {id:nextid.current, text:value, checked:false}
    nextid.current = nextid.current + 1
    setTodos( todos.concat(todo))
    localStorage.kim = JSON.stringify(todos.concat(todo))
    localStorage.nextid = JSON.stringify(nextid.current)
  }
    , [todos]) 
  
 
  const onRemove = useCallback(
    (id) => {
      setTodos(todos.filter( todo => todo.id !== id ))
      localStorage.kim = JSON.stringify(todos.filter( todo => todo.id !== id ))
    },[todos])
  
  
    useEffect(
      () => {
        const todo = localStorage.kim
        const localNextid = localStorage.nextid
        if (todo) {
          setTodos( todos.concat( JSON.parse(todo)) )
          nextid.current = JSON.parse(localNextid)
        }
      }
      , []
    )
    
    return (
    <TodoTemplate>
      <TodoInset onInsert={onInsert} />
      <TodoList todos={todos} onToggle={onToggle} onRemove={onRemove} />
    </TodoTemplate>
  );
};

export default App;