import React, {} from 'react';
import styled from 'styled-components';
import cn from 'classnames';
import {MdCheckBoxOutlineBlank, MdCheckBox} from 'react-icons/md'
import {FaEraser} from 'react-icons/fa'
const TodoListItemBlock = styled.div`
    padding:1rem;
    display:flex;
    align-items:center;
    &:nth-child(even) { background:#f8f9fa}
    & + &{ border-top:1px solid #dee2e6}
    .list { flex:1;
        display:flex;
        align-items:center;
        .red { color:#ff0000}
        .text { 
            magin-left:0.5rem;
            &.checked {text-decoration:line-through; 
            color:red;
            }
        }
    }
    .remove { color:#ff6b6b;
        cursor:pointer;
        font-size:1.5rem;
    }
`;

const TodoListItem = ({todo, onToggle, onRemove}) => {
    const {id, text, checked} = todo
   
    return (
        <TodoListItemBlock>
            <div className="list" onClick={()=>onToggle(id)}>
                { checked ? <MdCheckBox className='red' /> : <MdCheckBoxOutlineBlank />}
                <div className={cn('text',{checked})}>{text}</div>
            </div>
            <div className="remove" onClick={ ()=>onRemove(id) }>
                <FaEraser />
            </div>
        </TodoListItemBlock>
    );
};

export default TodoListItem;