import React, {useState, useRef} from 'react';
import styled from 'styled-components';
import {FaHotjar} from 'react-icons/fa';
// 리액트아이콘 경로는 {아이콘명}에서 앞 두글자를 소문자로
// rect-icons/md;

const FromBlock = styled.div`
.TodoInsert {
    background:#495057;
    display:flex;
    input { 
        background:none;
        outline:none;
        border:none;
        padding:0.5rem;
        font-size:1.125rem;
        line-height:1.5;
        color:#fff;
        flex:1;
        &::placeholder {color:#dee2e6;}
    }
    button {
        background:#000;
        outline:none;
        border:none;
        color:red;
        padding: 0px 20px;
        font-size:1.5rem;
        cursor:pointer;
        &:hover {background:#adb5bd;}
    }
}
`;

const TodoInset = ({onInsert}) => {
    const inputEl = useRef('')
    const [value, setValue] = useState('')
    const onChange = (e) => {
        setValue(e.target.value)
    }
    const onSubmit = (e) => {
        e.preventDefault()     
        onInsert(value)   
        setValue('')
        inputEl.current.focus()
    }

    return (
        <FromBlock>
            <form className="TodoInsert" onSubmit={onSubmit}>
                <input type="text" placeholder="공지해야 할 일" value={value} onChange={onChange} ref={inputEl} />
                <button type="submit"><FaHotjar /></button>
            </form>
        </FromBlock>
    );
};

export default TodoInset;